	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"NodeBots have been around for several years now, but with every presentation, there is always someone who asks, \"But... JavaScript?! Why on earth would you make robots with JavaScript?\"\n\nIt's a fair question, and one worthy of more than a hand-wavy answer about how it's cool... or something.\n\nIn this talk, let's discuss why, of all the languages on the planet, JavaScript is the perfect starting point for a future of robotics.\n\nAs a roboticist-turned-web-developer, I will provide some deep insights not only into the world of robotics, but also into JavaScript and its server-side cousin, Node.js. We'll talk about what JavaScript-enabled robots can already do, what they can't do yet, and what they might be able to do with a bit of elbow grease.\n\nAnd, if the demo deities are with us, we may even have a robot assistant on hand.\n\nRaquel Vélez\nNPM, INC.\n@rockbot\n\nRaquel Vélez is a Senior Software Developer at npm, Inc. in Oakland, CA. She has previously worked at institutions such as Caltech, NASA JPL, the MIT Lincoln Laboratory, and various universities in Europe. In her off time, you can find her baking, teaching NodeBots not to fall off of tables, and speaking. Also, hanging out with her hilarious husband and two cats dressed in dog suits.","meta":{"duration":"PT36M27S"},"presenter":{"name":"Raquel Velez"},"source":{"id":"3v75aX5-gSA","provider":"youtube"},"slug":"no-really-robots-and-javascript","tag":["JavaScript","Robot","Node.js"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/3v75aX5-gSA/default.jpg","height":90,"width":120}},"title":"\"No, Really... Robots and JavaScript?!\""}},{"node":{"description":"By popular demand, Defcon's angry little roboticist is back with more stories of robot designs gone awry that make practical lessons on making better robots. Drinking will happen: vodka-absconding scoundrels are not invited.\n\nThis talk will cover material assuming the average audience member is a relatively intelligent coder with a high-school physics/math background and has seen linear algebra/calculus before. The intent is to navigate people new to robotics around many lessons my teams and I learned the \"hard way,\" and to introduce enough vocabulary for a self-teaching student to bridge the gap between amateur and novice professional robotics. It will not cover why your Arduino doesn't work when you plugged your USB tx into your RS232 tx.","meta":{"duration":"PT49M51S"},"presenter":{"name":null},"source":{"id":"gFW0schumkE","provider":"youtube"},"slug":"katy-levinson-defcon-20-robots-youre-still-doing-it-wrong","tag":["Robot","Robots","Katy Levinson","DEFCON 20"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/gFW0schumkE/default.jpg","height":90,"width":120}},"title":"Katy Levinson Defcon 20 - Robots: You're Still Doing It Wrong"}}]}},"pathContext":{"slug":"robot","tag":"Robot"}}

